import { mapGetters, mapActions } from "vuex";
import memberCardAPI from "@/api/membershipCard";

export default {
  components: {},
  name: "member",
  data() {
    return {
      oneCode: "",
      // oneCode: "./images/oneCode.png",
      qrcode: "",
      // qrcode: "./images/qrcode.png",
      number: "6275980333894701904"
    };
  },
  methods: {
    ...mapActions([]),
    generateBar() {
      let params = {
        _width:250,
        _height:50,

        string: this.number,
        type: "bar"
      };
      memberCardAPI.generateImg(params).then(res => {
        let data = res.data;
        this.oneCode = "data:image/png;base64," + data.result;
      });
    },
    generateQR() {
      let params = {
        string: this.number,
        type: "qr"
      };
      memberCardAPI.generateImg(params).then(res => {
        let data = res.data;
        this.qrcode = "data:image/png;base64," + data.result;
      });
    },
    toRecord() {
      this.$router.push({
        path: "/memberRecord",
        query: {}
      });
    }
  },
  watch: {},
  computed: {
    ...mapGetters([])
  },
  created() {},
  mounted() {
    this.generateQR();
    this.generateBar();
    let elem = document.querySelector("#vux_view_box_body");
    setTimeout(() => {
      elem.style.paddingBottom = "0";
    }, 10);
  }
};
