import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';

Vue.use(VueAxios, axios);


let postService = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/'
})

let apiPostBaseUrl = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/open/VirtualMemberCardService/'
})
let apiGetBaseUrl = Vue.axios.create({
    method: 'POST',
    baseURL: '/api/open/VirtualMemberCardService/'
})

export default {
    generateImg: (opts) => {
        return apiPostBaseUrl({ url: 'generateImg', data: { ...opts } })
    },
    getCurrentMemberInfo: () => {
        return apiPostBaseUrl({ url: 'getCurrentMemberInfo'})
    },
    getMemberCardUsageRecord: () => {
        return apiPostBaseUrl({ url: 'getMemberCardUsageRecord' })
    },
    deleteMemberCardUsageRecord: (opts) => {
        return apiPostBaseUrl({ url: 'deleteMemberCardUsageRecord', data: { ...opts } })
    }
}